import React from 'react';
import { graphql } from 'gatsby';
import { useIntl, FormattedMessage } from 'gatsby-plugin-intl';

import { Query, AllorsSerialisedItem } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import Item from '../components/item';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { BreadCrumbs } from '../components/breadcrumbs';

function shuffle(src: AllorsSerialisedItem[]): AllorsSerialisedItem[] {
  const dst = src?.slice(0) ?? [];
  for (let i = dst.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [dst[i], dst[j]] = [dst[j], dst[i]];
  }
  return dst;
}

const OffersPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allAllorsSerialisedItem } = data;

  const validOffers = allAllorsSerialisedItem.nodes?.filter((v) => v.primaryPhoto?.file.childImageSharp);
  const offers = shuffle(validOffers);

  return (
    <Layout>
      <SEO title={intl.formatMessage({ id: 'home' })} description={intl.formatMessage({ id: 'home-description' })} />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="current-offers" />
                  <h1 className="page-title">
                    <FormattedMessage id="current-offers" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="current-offers-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          <section>
            <Container>
              <Row>
                {offers.map((offer) => {
                  return <Item key={offer.allorsId} item={offer}></Item>;
                })}
              </Row>
            </Container>
          </section>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allAllorsSerialisedItem(filter: { showOnFrontPage: { eq: true } }) {
      nodes {
        allorsId
        name
        itemNumber
        manufacturingYear
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        partWhereSerialisedItem {
          UnifiedGood {
            name
            slug
          }
        }
      }
    }
  }
`;

export default OffersPage;
